import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function ebitdaMarginTtm(tableName: string) {
  const title = "EBITDA Margin (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["ebitdaMarginTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Earnings before interest, taxes, depreciation, and amortization.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about EBITDA margin?`,
              value: info.row.original.financialData.companyRatios?.data?.ebitdaMarginTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={(info.row.original.financialData.companyRatios?.data?.ebitdaMarginTtm ?? 0) * 100}
        />
      )
    },
  })
}

export { ebitdaMarginTtm }
